body{
  margin: 0;
  padding: 0;
  background-color: aqua;
}
.container{
  background-color: rgb(248, 248, 255);
  width: 100%;
 height: 100vh;
  align-items: center;
  background-color: aqua;
}

div{
  width: 100%;
  height: 100vh;
 display: flex;
 flex-direction: column;
 gap: 10px;
 align-items: center;
 margin-top: 50px;
 

}

input{
border-radius: 5px;
height: 25px;
width: 250px;

}

button{
  color: rgb(252, 252, 252);
  background-color: rgb(3, 1, 26);
  border: none;
  border-radius: 5px;
  width: 100px;
  height: 30px;
  
}

section{
  background-color: rgb(86, 42, 245);
  display: flex;
  flex-direction: column;
  min-width: 320px;
  margin-top: 0;
  margin-bottom: 0;
  padding: 10px;
border: 1px solid rgb(241, 234, 192);
border-radius: 5px;
}

h4{
  margin-top: 0;
  color: rgb(248, 255, 255);
  margin-top: 0;
  margin-bottom: 0;
}